$primaryColor: '#0d2e68';

html,
body {
  height: 100%;
}

body {
  font-family: 'Noto Sans SC', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 27px;
}

h2 {
  font-style: normal;
  font-weight: 800;
  font-size: 16px;
  line-height: 24px;
  margin: 5px 0;
  color: $primaryColor;
}

p {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 18px;
  margin: 5px 0;
  color: $primaryColor;
}
